@tailwind base;
@tailwind components;
@tailwind utilities;

/* ‚ú® Í∏∞Î≥∏ Ìè∞Ìä∏ Î∞è Ïª¨Îü¨ ÏÑ∏ÌåÖ */
html,
body {
  margin: 0;
  padding: 0;
  background-color: #f8f7ff; /* ÎùºÎ≤§Îçî ÌôîÏù¥Ìä∏ */
  color: #1e1b2e; /* ÏßÑÌïú Î≥¥Îùº ÌöåÏÉâ ÌÖçÏä§Ìä∏ */
  font-family: "Pretendard", "Inter", "sans-serif";
  scroll-behavior: smooth;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
}

/* üíú Ïä§ÌÅ¨Î°§Î∞î Ïä§ÌÉÄÏùº (ÏùÄÏùÄÌïú ÎùºÎ≤§Îçî) */
::-webkit-scrollbar {
  width: 10px;
  height: 10px;
}
::-webkit-scrollbar-track {
  background: rgba(249, 247, 255, 0.8);
}
::-webkit-scrollbar-thumb {
  background: linear-gradient(180deg, #c4b5fd, #a78bfa);
  border-radius: 6px;
}
::-webkit-scrollbar-thumb:hover {
  background: linear-gradient(180deg, #a78bfa, #8b5cf6);
}

/* üí´ ÌÖçÏä§Ìä∏ ÏÑ†ÌÉù Í∞ïÏ°∞ÏÉâ */
::selection {
  background: rgba(167, 139, 250, 0.25);
  color: #1e1b2e;
}

/* üåà ÏÑπÏÖò Î¶¨Îπå Ïï†ÎãàÎ©îÏù¥ÏÖò */
.js-reveal {
  opacity: 0;
  transform: translateY(40px);
  transition: opacity 0.9s ease, transform 0.9s ease;
  will-change: opacity, transform;
}
.js-reveal.visible {
  opacity: 1;
  transform: translateY(0);
}

/* ‚öôÔ∏è Î†àÏù¥ÏïÑÏõÉ ÏïàÏ†ïÏÑ± */
main,
section {
  position: relative;
  z-index: 10;
}

/* üí° Î≤ÑÌäº Í∏∞Î≥∏ hover Ìö®Í≥º */
button {
  transition: all 0.25s ease;
}
button:focus {
  outline: none;
  box-shadow: 0 0 0 2px rgba(167, 139, 250, 0.4);
}
button:hover {
  transform: translateY(-2px);
}

/* üîó ÎßÅÌÅ¨ Í∏∞Î≥∏ Ïä§ÌÉÄÏùº */
a {
  color: inherit;
  text-decoration: none;
}
a:hover {
  color: #7c3aed;
}

/* üì± Î∞òÏùëÌòï Ïó¨Î∞± */
@media (max-width: 768px) {
  section {
    padding-left: 1.25rem;
    padding-right: 1.25rem;
  }
}

/* üíª ÏΩîÎìú Î∏îÎ°ù (API Îì±) */
pre {
  background: rgba(247, 245, 255, 0.9);
  border-radius: 0.75rem;
  padding: 1rem;
  color: #4b3f72;
  font-family: "JetBrains Mono", monospace;
  font-size: 0.9rem;
  overflow-x: auto;
  border: 1px solid rgba(167, 139, 250, 0.25);
}
code {
  color: #6d28d9;
}

/* üí´ Fade-in Ïú†Ìã∏Î¶¨Ìã∞ */
.fade-in {
  animation: fadeIn 1s ease forwards;
}
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(15px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* üå§Ô∏è Ïπ¥ÎìúÌòï ÏÑπÏÖò Í∑∏Î¶ºÏûê */
.card-shadow {
  box-shadow: 0 10px 30px rgba(167, 139, 250, 0.15);
  border: 1px solid rgba(167, 139, 250, 0.1);
  background: #ffffffcc;
  backdrop-filter: blur(12px);
}

/* üìú Ïù¥ÎØ∏ÏßÄ Î∞è ÎπÑÎîîÏò§ Î∞òÏùëÌòï */
img,
video {
  max-width: 100%;
  height: auto;
  display: block;
}
